
/**
 * @param {?} selector
 * @param {?} scrollWindow
 * @param {?} defaultElement
 * @return {?}
 */
export function resolveContainerElement(selector: string | any, scrollWindow, defaultElement): any {
  const /** @type {?} */ hasWindow = window && window.hasOwnProperty('document');
  const /** @type {?} */ containerIsString = selector && hasWindow && typeof(selector) === 'string';
  let /** @type {?} */ container = containerIsString
    ? window.document.querySelector(selector)
    : selector;
  if (!selector) {
    container = scrollWindow ? window : defaultElement;
  }
  return container;
}
